import { preloadAssets } from './preload';

const canvas: HTMLCanvasElement = document.createElement("canvas");
const ctx: CanvasRenderingContext2D = canvas.getContext(
  "2d"
) as CanvasRenderingContext2D;

const MOVE_STEP = 2;

const CANVAS_WIDTH = 416;
const CANVAS_HEIGHT = 416;

const BLOCK_SIZE = 8;
const CLUSTER_SIZE = 2;

const BULLET_SIZE = 8;
const BULLET_SPEED = 4;

const TANK_WIDTH = 32;
const TANK_HEIGHT = 32;

const SHOOT_COLDOWN = 200;

const MOVEMENT_SNAP_VALUE = BLOCK_SIZE * 2;

enum DIR {
  NONE = -1,
  UP = 0,
  RIGHT = 1,
  DOWN = 2,
  LEFT = 3,
}

enum BlockType {
  Brick,
  Steel,
  Tree,
  Water,
}

document.body.appendChild(canvas);

canvas.width = CANVAS_WIDTH;
canvas.height = CANVAS_HEIGHT;

function update() {
  player1.update();

  for (const bullet of bullets) {
    bullet.update();
  }
}

function render() {
  ctx.fillStyle = "#000";
  ctx.fillRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);

  player1.render();

  for (const bullet of bullets) {
    bullet.render();
  }
  for (const row of blocks) {
    for (const block of row) {
      if (block) {
        block.render();
      }
    }
  }
}

function tick() {
  update();
  render();

  window.setTimeout(tick, 1000 / 60);
}

interface XY {
  x: number;
  y: number;
}

class Body {
  pos: XY = {
    x: 0,
    y: 0,
  };
  size: XY = {
    x: 0,
    y: 0,
  };
  constructor(x = 0, y = 0, w = 0, h = 0) {
    this.pos.x = x;
    this.pos.y = y;
    this.size.x = w;
    this.size.y = h;
  }
  get t() {
    return this.pos.y;
  }
  get l() {
    return this.pos.x;
  }
  get b() {
    return this.pos.y + this.size.y;
  }
  get r() {
    return this.pos.x + this.size.x;
  }
  get center() {
    return {
      x: this.pos.x + this.size.x / 2,
      y: this.pos.y + this.size.y / 2,
    };
  }
}

class Tank extends Body {
  dir = DIR.RIGHT;
  lastShot = 0;

  constructor(x: number, y: number) {
    super(x, y, TANK_WIDTH, TANK_HEIGHT);
  }

  update() {
    // movement
    const newPos: XY = {
      x: this.pos.x,
      y: this.pos.y,
    };
    let newDir: DIR = DIR.NONE;
    if (keysPressed[KEYS.LEFT]) {
      newPos.x -= MOVE_STEP;
      this.pos.y = newPos.y = snapValue(this.pos.y);
      newDir = DIR.LEFT;
    } else if (keysPressed[KEYS.RIGHT]) {
      newPos.x += MOVE_STEP;
      this.pos.y = newPos.y = snapValue(this.pos.y);
      newDir = DIR.RIGHT;
    } else if (keysPressed[KEYS.UP]) {
      newPos.y -= MOVE_STEP;
      this.pos.x = newPos.x = snapValue(this.pos.x);
      newDir = DIR.UP;
    } else if (keysPressed[KEYS.DOWN]) {
      newPos.y += MOVE_STEP;
      this.pos.x = newPos.x = snapValue(this.pos.x);
      newDir = DIR.DOWN;
    }
    if (newDir !== DIR.NONE) {
      this.dir = newDir;

      if (this.move(newPos)) {
        this.pos = newPos;
      }
    }

    // shooting
    if (
      (!this.lastShot || Date.now() - this.lastShot >= SHOOT_COLDOWN) &&
      keysPressed[KEYS.SHOOT]
    ) {
      this.shoot();
    }
  }

  render() {
    const x = this.pos.x + this.size.x / 2;
    const y = this.pos.y + this.size.y / 2;
    ctx.save();
    ctx.translate(x, y);
    ctx.rotate((Math.PI / 2) * this.dir);
    ctx.translate(-x, -y);
    ctx.drawImage(
      sprites.player_1.img as CanvasImageSource,
      this.pos.x,
      this.pos.y
    );
    ctx.restore();
  }
  move(newPos: XY): boolean {
    const newBody = new Body(newPos.x, newPos.y, TANK_WIDTH, TANK_WIDTH);
    if (!bodyInBounds(newBody)) {
      return false;
    }

    const blocks = overlappingBlocks(newBody, 5);

    for (const block of blocks) {
      if (!block.isWalkable && AABBIntersects(newBody, block)) {
        return false;
      }
    }

    return true;
  }
  shoot() {
    bullets.push(
      new Bullet(
        this.center.x - BULLET_SIZE / 2,
        this.center.y - BULLET_SIZE / 2,
        this.dir
      )
    );
    this.lastShot = Date.now();
  }
}

class Block extends Body {
  x: number;
  y: number;
  type: BlockType;
  constructor(x: number, y: number, type: BlockType) {
    super(x * BLOCK_SIZE, y * BLOCK_SIZE, BLOCK_SIZE, BLOCK_SIZE);

    this.x = x;
    this.y = y;
    this.type = type;
  }
  render() {
    const gridX = Math.floor(this.pos.x / BLOCK_SIZE);
    const gridY = Math.floor(this.pos.y / BLOCK_SIZE);
    const spriteIndex = (gridY % 2) * 2 + (gridX % 2);

    switch (this.type) {
      case BlockType.Brick:
        ctx.drawImage(
          sprites[`brick_${spriteIndex + 1}`].img as CanvasImageSource,
          this.pos.x,
          this.pos.y
        );
        break;
      case BlockType.Steel:
        ctx.drawImage(
          sprites[`stone_${spriteIndex + 1}`].img as CanvasImageSource,
          this.pos.x,
          this.pos.y
        );
        break;
      case BlockType.Tree:
        ctx.drawImage(
          sprites[`tree_${spriteIndex + 1}`].img as CanvasImageSource,
          this.pos.x,
          this.pos.y
        );
        break;
      case BlockType.Water:
        ctx.drawImage(
          sprites[`water_${spriteIndex + 1}`].img as CanvasImageSource,
          this.pos.x,
          this.pos.y
        );
        break;
      default:
        ctx.fillStyle = "pink";
        ctx.fillRect(this.pos.x, this.pos.y, BLOCK_SIZE, BLOCK_SIZE);
    }
  }
  get isWalkable() {
    switch (this.type) {
      case BlockType.Tree:
        return true;
      default:
        return false;
    }
  }
  get isDamagable() {
    switch (this.type) {
      case BlockType.Brick:
        return true;
      default:
        return false;
    }
  }
  get isSolid() {
    switch (this.type) {
      case BlockType.Brick:
      case BlockType.Steel:
        return true;
      default:
        return false;
    }
  }
}

class Bullet extends Body {
  dir: DIR = DIR.NONE;

  constructor(x: number, y: number, dir: DIR) {
    super(x, y, BULLET_SIZE, BULLET_SIZE);

    this.dir = dir;
  }

  update() {
    switch (this.dir) {
      case DIR.LEFT:
        this.pos.x -= BULLET_SPEED;
        break;
      case DIR.UP:
        this.pos.y -= BULLET_SPEED;
        break;
      case DIR.RIGHT:
        this.pos.x += BULLET_SPEED;
        break;
      case DIR.DOWN:
        this.pos.y += BULLET_SPEED;
        break;
    }

    const b = overlappingBlocks(this, 2);

    if (b.length) {
      let hit = false;

      for (const block of b) {
        if (block.isSolid) {
          hit = true;
        }
        if (block.isDamagable) {
          blocks[block.y][block.x] = null;
          hit = true;
        }
      }

      if (hit) {
        bullets.splice(bullets.indexOf(this), 1);
      }
    }

    if (!bodyIsVisible(this)) {
      bullets.splice(bullets.indexOf(this), 1);
    }
  }
  render() {
    ctx.fillStyle = "#fff";
    ctx.fillRect(this.pos.x, this.pos.y, BULLET_SIZE, BULLET_SIZE);
  }
}

function AABBIntersects(A: Body, B: Body): boolean {
  return !(A.l >= B.r || B.l >= A.r || A.t >= B.b || B.t >= A.b);
}

function snapValue(value: number): number {
  const snapped = Math.round(value / MOVEMENT_SNAP_VALUE);
  return snapped * MOVEMENT_SNAP_VALUE;
}

function overlappingBlocks(body: Body, radius = 2): Block[] {
  const lPos = Math.floor(body.l / BLOCK_SIZE);
  const tPos = Math.floor(body.t / BLOCK_SIZE);

  const items: Array<Block> = [];

  for (let x = lPos; x < lPos + radius; x++) {
    for (let y = tPos; y < tPos + radius; y++) {
      if (!blockIndexInBounds(x, y)) {
        continue;
      }
      if (blocks[y][x]) {
        items.push(blocks[y][x] as Block);
      }
    }
  }

  return items;
}

function blockIndexInBounds(x: number, y: number) {
  return x >= 0 && x < 52 && y >= 0 && y < 52;
}

function bodyInBounds(body: Body) {
  return (
    body.l >= 0 &&
    body.t >= 0 &&
    body.b <= CANVAS_HEIGHT &&
    body.r <= CANVAS_WIDTH
  );
}

function bodyIsVisible(body: Body) {
  return (
    body.l < CANVAS_WIDTH && body.r > 0 && body.t < CANVAS_HEIGHT && body.b > 0
  );
}

const KEYS = {
  LEFT: "ArrowLeft",
  RIGHT: "ArrowRight",
  UP: "ArrowUp",
  DOWN: "ArrowDown",
  SHOOT: "Space",
};
const keysPressed: { [key: string]: boolean } = {};

window.addEventListener("keydown", function (e) {
  keysPressed[e.code] = true;
});

window.addEventListener("keyup", function (e) {
  keysPressed[e.code] = false;
});

const player1 = new Tank(0, 0);
const blocks: Array<Array<Block | null>> = new Array(52)
  .fill(null)
  .map(() => new Array(52).fill(null));

const bullets: Bullet[] = [];

// for (let i = 0; i < 10; i++) {
buildCluster(2, 2, BlockType.Brick);
buildCluster(2, 3, BlockType.Brick);
buildCluster(3, 2, BlockType.Brick);
buildCluster(3, 3, BlockType.Brick);
// }

buildCluster(4, 4, BlockType.Steel);
buildCluster(4, 5, BlockType.Steel);
buildCluster(5, 4, BlockType.Steel);
buildCluster(5, 5, BlockType.Steel);

buildCluster(6, 6, BlockType.Tree);
buildCluster(6, 7, BlockType.Tree);
buildCluster(7, 6, BlockType.Tree);
buildCluster(7, 7, BlockType.Tree);

buildCluster(8, 8, BlockType.Water);
buildCluster(8, 9, BlockType.Water);
buildCluster(9, 8, BlockType.Water);
buildCluster(9, 9, BlockType.Water);

function buildCluster(gridX: number, gridY: number, blockType: BlockType) {
  for (let yOffset = 0; yOffset < CLUSTER_SIZE; yOffset++) {
    for (let xOffset = 0; xOffset < CLUSTER_SIZE; xOffset++) {
      const yIndex = gridY * CLUSTER_SIZE + yOffset;
      const xIndex = gridX * CLUSTER_SIZE + xOffset;

      blocks[yIndex][xIndex] = new Block(xIndex, yIndex, blockType);
    }
  }
}

preloadAssets().then(tick);
